import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { ListProvider } from '../../providers/list/list';
import { Observable } from 'rxjs/Observable';
import { ProductsPage } from '../products/products';


@IonicPage()
@Component({
  selector: 'page-list',
  templateUrl: 'list.html',
})
export class ListPage {
  
  lists: Observable<any>;
  
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public alertCtrl: AlertController , private provider: ListProvider,
    private toast: ToastController) {

    }
    
    ionViewDidLoad() {
      console.log('ionViewDidLoad ListPage');
      this.lists = this.provider.getAll();
    }
    
    newList(){
      console.log("createList function");
      
      const prompt = this.alertCtrl.create({
        title: 'Nova Lista',
        message: "Digite o nome da nova lista que você deseja criar",
        inputs: [
          {
            name: 'name',
            placeholder: 'Nome da lista'
          },
        ],
        buttons: [
          {
            text: 'Cancelar',
            handler: data => {
              console.log('Cancel clicked');
            }
          },
          {
            text: 'Salvar',
            handler: data => {
              console.log('Saved clicked');
              if(data.name){
                console.log('Nome = '+data.name);
                this.onSubmit(data);
              }else{
                this.presentAlert();
              }
            }
          }
        ]
      });
      prompt.present();
      this.navCtrl.push(ListPage, {} );
      
      
    }
    
    private presentAlert() {
      let alert = this.alertCtrl.create({
        title: 'Insira um nome',
        subTitle: 'Campo obrigatório',
        buttons: [{
          text:'Ok',
          handler:data=>{
            this.newList();
          }
        }]
      });
      alert.present();
    }
    
    editList(List: any) {
      
      const prompt = this.alertCtrl.create({
        title: 'Editar Lista',
        message: "Digite o nome da lista que você deseja editar",
        inputs: [
          {
            name: 'name',
            placeholder: 'Nome da lista',
            value: List.name
          },
        ],
        buttons: [
          {
            text: 'Cancelar',
            handler: data => {
              console.log('Cancel clicked');
            }
          },
          {
            text: 'Editar',
            handler: data => {
              data.key = (List.key) ? (List.key) : null;
              if(data.name){
                console.log('Nome = '+data.name);
                this.onSubmit(data);
              }else{
                this.presentAlert();
              }
            }
          }
        ]
      });
      prompt.present();
      this.navCtrl.push(ListPage, {} );

    }
    
    removeList(key: string) {
      if (key) {
        this.provider.remove(key)
        .then(() => {
          this.toast.create({ message: 'Lista removida com sucesso.', duration: 3000 }).present();
        })
        .catch(() => {
          this.toast.create({ message: 'Erro ao remover a lista.', duration: 3000 }).present();
        });
      }
    }
    
    onSubmit(data) {
      if (data) {
        this.provider.save(data)
        .then(() => {
          this.toast.create({ message: 'Lista salva com sucesso.', duration: 3000 }).present();
          this.navCtrl.pop();
        })
        .catch((e) => {
          this.toast.create({ message: 'Erro ao salvar a lista.', duration: 3000 }).present();
          console.error(e);
        })
      }
    }

    openList(list_key){
      this.navCtrl.push(ProductsPage, {list_key: list_key} );
    }
    
    
    
  }
  


