import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ProductsProvider } from '../../providers/products/products';
import { HelperProvider } from '../../providers/helper/helper';

/**
* Generated class for the ProductCreatePage page.
*
* See https://ionicframework.com/docs/components/#navigation for more info on
* Ionic pages and navigation.
*/

@IonicPage()
@Component({
  selector: 'page-product-create',
  templateUrl: 'product-create.html',
})
export class ProductCreatePage {
  public product = {
    quantity: 1,
    price: "",
    total: ""
  };
  private list_id;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public formBuilder: FormBuilder,private provider: ProductsProvider,private toast: ToastController,
    public helper: HelperProvider) {
      this.list_id = this.navParams.get('list_id');
      
      if(this.navParams.get('product')){
        console.log("PRODUCT OBJECT EXISTS (Y) ");
        this.product = this.navParams.get('product');
      }

      console.log("THIS.LIST_ID = "+this.list_id);
    }
    
    ionViewDidLoad() {
      console.log('ionViewDidLoad ProductCreatePage');
    }
    
    addProduct(){
      console.log("addProduct = "+JSON.stringify(this.product));
      let data = this.product;
      if (data) {
        this.provider.save(data)
        .then(() => {
          // this.toast.create({ message: 'Produto salvo com sucesso.', duration: 3000 }).present();
          this.navCtrl.pop();
        })
        .catch((e) => {
          this.toast.create({ message: 'Erro ao salvar o produto.', duration: 3000 }).present();
          console.error(e);
        })
      }

      
    }
    sub(){
      if(this.product.quantity>1)
      this.product.quantity--;
      
      this.calc_prices();
    }
    calc_prices(){
      var prod_price = this.product.price;
      prod_price = prod_price.toString();
      var price_dollar = prod_price.replace(/\./g, '');
      console.log("number replace dots = "+price_dollar);
      var price_comma = price_dollar.replace(/\,/g, '.');
      console.log("number price_comma = "+price_comma);
      let multiplication = this.product.quantity * parseFloat(price_comma);
      console.log("number requantity x parse float dollar = "+multiplication);
      let multiplication_num = multiplication.toFixed(2);
      console.log("number fixed 2 = "+multiplication_num);
      let multiplication_str = (multiplication_num.toString()=="NaN") ? "0,00" : multiplication_num.toString();
      console.log("number string = "+multiplication_str);
      let total_real = this.getMoney(multiplication_str);
      console.log("number get Money = "+total_real);
      let total_real_format = this.formatReal(total_real);
      console.log("number format real = "+total_real_format);
      this.product.total = total_real_format.toString();
      console.log("total_real_format to string = "+this.product.total);
    }
    add(){
      this.product.quantity++;
      this.calc_prices();
    }

    
    
    getMoney( str )
    {
      return str.replace(/[\D]+/g,'');
    }
    formatReal( int )
    {
      var tmp = int+'';
      tmp = tmp.replace(/([0-9]{2})$/g, ",$1");
      if( tmp.length > 6 )
      tmp = tmp.replace(/([0-9]{3}),([0-9]{2}$)/g, ".$1,$2");
      
      return tmp;
    }
    
    
  }
  


