import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthProvider } from '../auth/auth';
import { AngularFireDatabase } from '@angular/fire/database';
import { HelperProvider } from '../helper/helper';

/*
  Generated class for the ProductsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ProductsProvider {
  private PATH;
  private products_list;
  constructor(private authProvider: AuthProvider , private db: AngularFireDatabase,
    public helper: HelperProvider) {
    console.log('Hello ProductsProvider Provider');
  }
  getPath(){
    return this.PATH;
  }
  setPath(list_id){
    this.PATH = 'list/'+list_id+"/products";
    let user_profile_path = this.authProvider.PATH;
    let user_id = localStorage.getItem("user_id");
    this.PATH = user_profile_path+"/"+user_id+"/"+this.PATH;
    console.log("this PATH = "+this.PATH);
  }
  getAllById(list_id) {
    this.setPath(list_id);
    return this.db.list(this.PATH, ref => ref.orderByChild('id'))
    .snapshotChanges()
    .map(changes => {
      return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
    })

  }
  remove(key: string) {
    return this.db.list(this.PATH).remove(key);
  }

  save(list: any) {
    return new Promise((resolve, reject) => {
      if (list.key) {
        this.db.list(this.PATH)
          .update(list.key, list)
          .then(() => resolve())
          .catch((e) => reject(e));
      } else {
        this.db.list(this.PATH)
          .push(list)
          .then(() => resolve());
      }
    })
  }

  setTotalPrice(products){
    let total=0;
    let total_numbers = 0.00;
    for (let product in products) {
      let price_numbers = this.helper.getMoney(product);
      
      total += parseFloat(this.helper.formatDollarStr(price_numbers));
      console.log("TOTAL = "+total)
    }
    total_numbers = this.helper.getMoney(total);
    return this.helper.formatReal(total_numbers);
  }

}



