import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ToastController } from 'ionic-angular';
import { ProductsProvider } from '../../providers/products/products';
import { Observable } from 'rxjs/Observable';
import { ProductCreatePage } from '../product-create/product-create';
import { HelperProvider } from '../../providers/helper/helper';
import { AngularFireDatabase } from '@angular/fire/database';

/**
* Generated class for the ProductsPage page.
*
* See https://ionicframework.com/docs/components/#navigation for more info on
* Ionic pages and navigation.
*/

@IonicPage()
@Component({
  selector: 'page-products',
  templateUrl: 'products.html',
})
export class ProductsPage {
  public list_id;
  public total_price = 0.00;
  products: Observable<any>;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    private provider: ProductsProvider,public modalCtrl: ModalController , 
    public helper: HelperProvider, private db: AngularFireDatabase,
    private toast: ToastController) {
      this.list_id = this.navParams.get('list_key');
      
      // console.log("this.list_id ="+this.list_id );
      
    }
    
    ionViewDidLoad() {
      this.products = this.provider.getAllById(this.list_id);
      
      this.getTotalPrice();
    }
    
    newProduct(){
      let productModal = this.modalCtrl.create(ProductCreatePage, { list_id: this.list_id });
      productModal.present();
      productModal.onDidDismiss( () => {
        this.getTotalPrice();
      });
    }
    editProduct(product_obj){
      let productModal = this.modalCtrl.create(ProductCreatePage, 
        { list_id: this.list_id , product: product_obj }
        );
        productModal.present();
        productModal.onDidDismiss( () => {
          // console.log("onDidDismiss productModal");
          this.getTotalPrice();
        });
      }
      removeProduct(key: string){
        if (key) {
          this.provider.remove(key)
          .then(() => {
            this.toast.create({ message: 'Produto removido com sucesso.', duration: 3000 }).present();
          })
          .catch(() => {
            this.toast.create({ message: 'Erro ao remover o produto.', duration: 3000 }).present();
          });
        }
        this.getTotalPrice();
      }
      getTotalPrice(){
        let total = 0.00;
        let PATH = this.provider.getPath();
        return this.db.list(PATH).valueChanges().subscribe(items => {
          items.forEach(item => {
            console.log("ITEM total:", item['total']);
            let total_item = item['total'];
            console.log("total_item = "+total_item);
            var dollar_float = this.helper.real_to_dollar(total_item);
           
            total += dollar_float;
            console.log("TOTAL = "+total);
          });
          this.total_price = total;
        });
        
      }
      
      check_item(item){
        item["is_checked"] = (item["is_checked"]) ? false : true;
        
        console.log("check_item = "+ JSON.stringify(item) );
        
        this.provider.save(item)
        .then(() => {
          console.log("Checked Item OK");
        })
        .catch((e) => {
          this.toast.create({ message: 'Erro ao marcar item.', duration: 3000 }).present();
          console.error(e);
        })
        this.getTotalPrice();
        
      }
      
      
      
    }
    


