import firebase from 'firebase/app'
import 'firebase/app'
import 'firebase/auth'
import 'firebase/firestore'
import { AngularFireModule } from '@angular/fire';
import { SignupPage } from '../pages/signup/signup';
import { ResetPasswordPage } from '../pages/reset-password/reset-password';
import { HelperProvider } from '../providers/helper/helper';
import { ProductsPage } from '../pages/products/products';
import { ProductsProvider } from '../providers/products/products';
import { ProductCreatePage } from '../pages/product-create/product-create';
import { BrMaskerModule } from 'brmasker-ionic-3';
import { registerLocaleData } from '@angular/common';
import localePt from '@angular/common/locales/pt';
import { SplashPage } from '../pages/splash/splash';


export const firebaseConfig = {
  apiKey: "AIzaSyC_NW-kltN7kQiYllT8tKl1rpVWhbEiv5Q",
  authDomain: "listadecompras-57f68.firebaseapp.com",
  databaseURL: "https://listadecompras-57f68.firebaseio.com",
  projectId: "listadecompras-57f68",
  storageBucket: "listadecompras-57f68.appspot.com",
  messagingSenderId: "731232975029",
  appId: "1:731232975029:web:7e9273c1ba73b505"
};
firebase.initializeApp(firebaseConfig);
registerLocaleData(localePt, 'pt-BR');

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    SignupPage,
    ResetPasswordPage,
    ProductsPage,
    ProductCreatePage,
    SplashPage
  ],
  imports: [
    BrMaskerModule,
    AngularFireAuthModule,
    AngularFireModule.initializeApp(firebaseConfig),
    BrowserModule,
    IonicModule.forRoot(MyApp,
      {
        backButtonText: '',
        backButtonIcon: 'ios-arrow-back',
        iconMode: 'md'
      }),
      AngularFireDatabaseModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    SignupPage,
    ResetPasswordPage,
    ProductsPage,
    ProductCreatePage,
    SplashPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ListProvider,
    AuthProvider,
    HelperProvider,
    ProductsProvider,
    { provide: LOCALE_ID, useValue: "pt-BR" }
  ]
})
export class AppModule {}



