import { Injectable } from '@angular/core';
import { AngularFireDatabase } from '@angular/fire/database';
import 'rxjs/add/operator/map';
import { AuthProvider } from '../auth/auth';
import { HelperProvider } from '../helper/helper';

@Injectable()
export class ListProvider {
  private PATH;

  constructor(private db: AngularFireDatabase , 
    private authProvider: AuthProvider, private helper: HelperProvider) {
    console.log('Hello ListProvider Provider');
    
  }
  setPath(){
    this.PATH = 'list/';
    let user_profile_path = this.authProvider.PATH;
    let user_id = localStorage.getItem("user_id");
    this.PATH = user_profile_path+"/"+user_id+"/"+this.PATH;
    console.log("this PATH = "+this.PATH);
  }
  getAll() {
    this.setPath();
    return this.db.list(this.PATH, ref => ref.orderByChild('id'))
    .snapshotChanges()
    .map(changes => {
      return changes.map(c => ({ key: c.payload.key, ...c.payload.val() }));
    })
  }

  save(list: any) {
    return new Promise((resolve, reject) => {
      if (list.key) {
        this.db.list(this.PATH)
          .update(list.key, { name: list.name})
          .then(() => resolve())
          .catch((e) => reject(e));
      } else {
        this.db.list(this.PATH)
          .push({ name: list.name, })
          .then(() => resolve());
      }
    })
  }

  remove(key: string) {
    return this.db.list(this.PATH).remove(key);
  }

}



