import { Injectable } from '@angular/core';


@Injectable()
export class HelperProvider {
  
  constructor() {
    console.log('Hello HelperProvider Provider');
  }
  
  getFirebaseMessage(code){
    
    let msg;
    
    switch (code) {
      case "auth/email-already-exists":
      msg =  "Esse e-mail já está em uso.";
      break;
      case "auth/email-already-in-use":
      msg =  "Esse e-mail já está em uso.";
      break;
      case "auth/user-not-found":
      msg =  "E-mail ou Senha não encontrados.";
      break;
      case "auth/wrong-password":
      msg =  "E-mail ou Senha não encontrados.";
      break;
      default:
      msg = code;
      break;
    }
    return msg;
  }
  
  getMoney( str )
  {
    return parseInt( str.replace(/[\D]+/g,'') );
  }
  formatRealPtBr( int ){
    return int.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }); 
  }
  formatReal( int )
  {
    var tmp = int+'';
    tmp = tmp.replace(/([0-9]{2})$/g, ",$1");
    if( tmp.length > 6 )
    tmp = tmp.replace(/([0-9]{3}),([0-9]{2}$)/g, ".$1,$2");
    
    return tmp;
  }
  formatDollarStr(number){
    return number.toLocaleString('en-US');
  }
  formatMoney(amount, decimalCount = 2, decimal = ".", thousands = ",") {
    decimalCount = Math.abs(decimalCount);
    decimalCount = isNaN(decimalCount) ? 2 : decimalCount;
    
    const negativeSign = amount < 0 ? "-" : "";
    
    let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
    let j = (i.length > 3) ? i.length % 3 : 0;
    
    return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - parseInt(i)).toFixed(decimalCount).slice(2) : "");
    
  }

  created_at_date(){
    let date_now = new Date().toISOString();
    console.log("DATE NOW = "+date_now);
    return date_now;
  }

  real_to_dollar(price){
    price = price.toString();
    let replace_dots = price.replace(/\./g, '');
    console.log("LET replace_dots = "+replace_dots);
    var replace_comma = replace_dots.replace(/\,/g, '.');
    console.log("replace_comma  = "+replace_comma);
    var dollar_float = parseFloat(replace_comma);
    console.log("DOLLAR_FLOAT = "+dollar_float);
    return dollar_float;
  }
  
}



